import axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Dialog from '@radix-ui/react-dialog';

const editInputStyle = {
    display: 'grid',
    gap: '0.8rem',
    gridTemplateColumns: '1fr 1fr',
    alignItems: 'center',
    marginBottom: '0.3rem'
}

const inputStyle = {
    padding: '0.5rem 0.8rem',
    borderRadius: '0.5rem',
    border: '1px solid var(--clr-border)',
    width: '300px',
}

const labelStyle = {
    textAlign: 'right',
    fontSize: '0.9rem',
    fontWeight: 'var(--fw-md)'
}

function AddAction() {

  const serverApi = "/api/add"

  const [input, setInput] = useState({})
  const navigate=useNavigate()

  const changeMyData = (event) => {
    setInput({
        ...input,[event.target.name]:event.target.value,
    })
  }

  const handleSubmit = (event)=>{
    axios.post(serverApi, input)
    .then(response => {
        navigate('/dashboard')
    })
  }

  return (
    <Dialog.Root>
        <Dialog.Trigger asChild>
            <button className='btn btn-add' data-icon='add-icon'>Add</button>
        </Dialog.Trigger>
        <Dialog.Portal>
            <Dialog.Overlay className="DialogOverlay" />
            <Dialog.Content className="DialogContent">
                <div className='add-icon'></div>
                <Dialog.Title className="DialogTitle">Add Movie Details</Dialog.Title>
                <Dialog.Description className="DialogDescription">
                    Add a new movie detail here. Click Add details when you're done.
                </Dialog.Description>
                    <form onSubmit={handleSubmit}>
                        <div style={{display: 'grid', gap:'1.25rem'}} className='form-body'>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="movieid-input">Movie ID</label>
                                <input style={inputStyle} name='key1' id='movieid-input' type="text" placeholder='Movie Id' onChange={changeMyData} />
                            </div>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="movie_name-input">Movie Name</label>
                                <input style={inputStyle} name='key2' id='movie_name-input' type="text" placeholder='Movie Name' onChange={changeMyData} />
                            </div>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="release-input">Release Year</label>
                                <input style={inputStyle} name='key3' id='release-input' type="text" placeholder='Year' onChange={changeMyData} />
                            </div>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="rating-input">Rating</label>
                                <input style={inputStyle} name='key4' id='rating-input' type="text" placeholder='Rating' onChange={changeMyData} />
                            </div>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="director-input">Movie Director</label>
                                <input style={inputStyle} name='key5' id='director-input' type="text" placeholder='Movie Director' onChange={changeMyData} />
                            </div>
                            <div style={editInputStyle}>
                                <label style={labelStyle} htmlFor="language-input">Language</label>
                                <input style={inputStyle} name='key6' id='language-input' type="text" placeholder='Language' onChange={changeMyData} />
                            </div>
                            <div style={{display: 'grid', gap: '1rem', gridTemplateColumns: '1fr 1fr'}} className='form-action'>
                                <Dialog.Close asChild>
                                    <button type='submit' className="btn btn-cancel" data-icon='close' aria-label="Close">
                                        Cancel
                                    </button>
                                </Dialog.Close>
                                <button className='btn btn-save'>Add Details</button>
                            </div>
                        </div>
                    </form>
            </Dialog.Content>
        </Dialog.Portal>
    </Dialog.Root>
  )
}

export {AddAction}