import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import DeleteAction from './DeleteAction';


const StyledTableCell = styled(TableCell)(() => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: '#F9FAFB',
    color: 'var(--clr-text-dark)',
    padding: '0.9rem 1rem',
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 15,
    padding: '0.8rem 1rem',
  },
}));

const StyledTableRow = styled(TableRow)(() => ({
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

function DashbaordTable() {
  const apiUrl = "/api/viewall"

  const [data, setData] = useState([])

  useEffect(() =>{
    axios.post(apiUrl).then(
        (response) =>{
            setData(response.data)
        }
      )
    },[]
  )
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 700 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Sl.no</StyledTableCell>
            <StyledTableCell>Movie Id</StyledTableCell>
            <StyledTableCell>Movie Name</StyledTableCell>
            <StyledTableCell>Rating</StyledTableCell>
            <StyledTableCell>Release Year</StyledTableCell>
            <StyledTableCell>Movie Director</StyledTableCell>
            <StyledTableCell>Langauge</StyledTableCell>
            <StyledTableCell></StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row, index) => (
            <StyledTableRow key={row.movie_id}>  
              <StyledTableCell style={{fontWeight: 'var(--fw-md)'}}>{index + 1}</StyledTableCell>            
              <StyledTableCell>{row.movie_id}</StyledTableCell>
              <StyledTableCell style={{fontWeight: 'var(--fw-sb)'}}>{row.movie_name}</StyledTableCell>
              <StyledTableCell>{row.rating}</StyledTableCell>
              <StyledTableCell>{row.release_year}</StyledTableCell>
              <StyledTableCell>{row.movie_director}</StyledTableCell>
              <StyledTableCell>{row.language}</StyledTableCell>
              <StyledTableCell>< DeleteAction data={row} /></StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export {DashbaordTable}